import Page from 'components/Page';
import React from 'react';
import { format } from 'date-fns';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import PageSpinner from 'components/PageSpinner';
import { findWeeklyReport, findTrucks, downloadWeeklyReport, } from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import load_rates from "../api/rates";
class FactoringReportsPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            truck : 'nn',
            rate : 'nn',
            from_date: '',
            to_date: '',
            xtoken:'',
            msg: '',
            error: '',
            fileurl:'',
            downloadFile:true,
            preload:false,
            trips: [],
            deductions: [],
            scheduled: [],
            fuel: [],
            summations: {
                a:'0.00',
                b:'0.00',
                c:'0.00',
                d:'0.00',
                e:'0.00',
            },
            trucks:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findTrucks(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    trucks:response.data,
                });
            }).catch((respError) => {});
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }
    format_date_str = (str) => {
      let new_str = str.split('-');
      let day = new_str[2].slice(0, 2);
      return format(new Date(new_str[0], new_str[1], day), 'MM/dd/yyyy');
    }
    firstUpper = (str) => {
        let string = str.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }
    handleStartDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, from_date: v,});
    }
    handleEndDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, to_date: v,});
    }
    handleTruckInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, truck: v,});
    }
    handleRateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, rate: v,});
    }
    handleSaveChanges = () => {
      this.setState({...this.state, preload:true});
    //   console.log('this state', this.state);
    findWeeklyReport(this.state.xtoken, this.state)
      .then((response) => {
        //   console.log('exp response', response);
          if( response.status === 200)
          {
              this.setState({
                  ...this.state,
                  msg: response.message,
                  trips: response.trips,
                  deductions: response.deductions,
                  scheduled: response.scheduled,
                  fuel: response.fuel,
                  summations: response.summations,
                  preload:false,
                  error: '',
              });
              this.forceUpdate();
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  preload:false,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
              preload:false,
          });
          console.log('error', error);
      });
    }
    handleDownloadReport = () => {
      this.setState({...this.state, preload:true});
      //console.log('this state', this.state);
      downloadWeeklyReport(this.state.xtoken, this.state)
      .then((response) => {
          // console.log('download response', response);
          if( response.status === 200)
          {
            const url = response.fileurl;
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'statement');
            link.setAttribute('target', '_blank');
            link.click();
            this.setState({
                ...this.state,
                preload:false,
                fileurl: response.fileurl,
                error: '',
            });
            this.forceUpdate();
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  preload:false,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
              preload:false,
          });
          console.log('error', error);
      });
    }
    render() {
      return (
        <Page title="Factoring reports" breadcrumbs={[{ name: 'Reports', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>
                Factoring reports
                {this.state.preload && (<PageSpinner/>)}
                </CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Start date*</Label>
                      <Input onChange={this.handleStartDateInput}
                        type="date" 
                        value={this.state.from_date}
                        name="fromdate"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">End date*</Label>
                      <Input onChange={this.handleEndDateInput}
                        type="date" 
                        value={this.state.to_date}
                        name="todate"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">Truck*</Label>
                      <Input onChange={this.handleTruckInput} value={this.state.truck} defaultValue={this.state.truck} type="select" name="driver">
                        <option value="nn" disabled={true}>Select truck</option>
                        {
                            this.state.trucks.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{this.firstUpper(stt.make) + " - " + stt.number}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">Rate*</Label>
                      <Input onChange={this.handleRateInput} value={this.state.rate} defaultValue={this.state.rate} type="select" name="driver">
                        <option value="nn" disabled={true}>Select rate</option>
                        {
                            load_rates.data.map((stt, idx) => (
                                <option key={idx} value={stt.value}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <Row>
                        <Col xl={6} lg={12} md={12}>
                            <FormGroup>
                                <Button type="button" onClick={this.handleSaveChanges}>Run report</Button>
                            </FormGroup>
                        </Col>
                        <Col xl={6} lg={12} md={12}>
                            <FormGroup>
                                <Button type="button" onClick={this.handleDownloadReport}>Download</Button>
                            </FormGroup>
                        </Col>
                    </Row>
                  </Form>
                </CardBody>
              </Card>
            </Col>
            <Col xl={8} lg={12} md={12}>
              <Card>
                <CardHeader className="statement-head">Trips</CardHeader>
                <CardBody>
                    <Table responsive>
                        <thead>
                        <tr>
                            <th>Trip</th>
                            <th>Description</th>
                            <th>Mileage</th>
                            <th>Date</th>
                            <th>Load rate</th>
                            <th>Net rate</th>
                        </tr>
                        </thead>
                        <tbody>
                        { this.state.trips.map((trp, idx) => (
                            <tr>
                                <th scope="row">{trp.number}</th>
                                <td>{trp.origin} - {trp.destination}</td>
                                <td>{trp.mileage}</td>
                                <td>{this.format_date_str(trp.created_at)}</td>
                                <td>${trp.rate}</td>
                                <td>${trp.net}</td>
                            </tr>
                        ))}
                        <tr>
                            <th colSpan="4"></th>
                            <td><b>Total</b></td>
                            <td><b>${this.state.summations.a}</b></td>
                        </tr>
                        </tbody>
                    </Table>
                </CardBody>
                {/* deductions */}
                <CardHeader className="statement-head">Deductions</CardHeader>
                <CardBody>
                    <Table responsive>
                        <thead>
                        <tr>
                            <th>Description</th>
                            <th>Date</th>
                            <th>Misc. amount</th>
                            <th>Amount</th>
                            <th>Subtotal</th>
                        </tr>
                        </thead>
                        <tbody>
                        {this.state.deductions.map((ded, idx) => (
                            <tr>
                                <td>{ded.description}</td>
                                <td>{this.format_date_str(ded.created_at)}</td>
                                <td>${ded.misc_amount_f}</td>
                                <td>${ded.amount_f}</td>
                                <td>${ded.total}</td>
                            </tr>
                        ))}
                        <tr>
                            <th colSpan="3"></th>
                            <td><b>Total</b></td>
                            <td><b>(${this.state.summations.b})</b></td>
                        </tr>
                        </tbody>
                    </Table>
                </CardBody>
                {/* fuel */}
                <CardHeader className="statement-head">Fuel Card</CardHeader>
                <CardBody>
                    <Table responsive>
                        <thead>
                        <tr>
                            <th>Description</th>
                            <th>City</th>
                            <th>State</th>
                            <th>Date</th>
                            <th>Misc. cost</th>
                            <th>Fuel cost</th>
                            <th>Subtotal</th>
                        </tr>
                        </thead>
                        <tbody>
                        { this.state.fuel.map((fl, idx) => (
                            <tr>
                                <td>{fl.description}</td>
                                <td>{fl.city}</td>
                                <td>{fl.state}</td>
                                <td>{this.format_date_str(fl.created_at)}</td>
                                <td>${fl.misc_amount_f}</td>
                                <td>${fl.amount_f}</td>
                                <td>${fl.total}</td>
                            </tr>
                        ))}
                        <tr>
                            <th colSpan="5"></th>
                            <td><b>Total</b></td>
                            <td><b>(${this.state.summations.c})</b></td>
                        </tr>
                        </tbody>
                    </Table>
                </CardBody>
                {/* scheduled */}
                <CardHeader className="statement-head">Scheduled deductions</CardHeader>
                <CardBody>
                    <Table responsive>
                        <thead>
                        <tr>
                            <th>Description</th>
                            <th>Date</th>
                            <th>Amount</th>
                            <th>Subtotal</th>
                        </tr>
                        </thead>
                        <tbody>
                        {this.state.scheduled.map((sch, idx) => (
                            <tr>
                                <td>{sch.description}</td>
                                <td>{this.format_date_str(sch.created_at)}</td>
                                <td>${sch.amount_f}</td>
                                <td>${sch.total}</td>
                            </tr>
                        ))}
                        <tr>
                            <td colSpan="2"></td>
                            <td><b>Total</b></td>
                            <td><b>(${this.state.summations.d})</b></td>
                        </tr>
                        <tr>
                            <td colSpan="4"></td>
                        </tr>
                        <tr className="statement-check">
                            <td colSpan="2"></td>
                            <td><b>Check Amount</b></td>
                            <td><b>${this.state.summations.e}</b></td>
                        </tr>
                        </tbody>
                    </Table>
                </CardBody>
              </Card>
            </Col>
        </Row>
        </Page>
      );
    }
}
export default FactoringReportsPage;
